.TodoItem {
    background: #fff;
    border: none;
    border-bottom: 0.1rem solid #ddd;

    &:last-child {
        border-bottom: none;
    }
}

.TodoItem____done {
    @extend .TodoItem;

    text-decoration: line-through;
    color: #aaa;
}

.TodoItem__content {
    margin: 1rem 0;
    min-height: 2rem;
}

.checkbox {
    margin-right: 0.4rem;
}
